.d-flex{
    display: flex;
}

.space-around{
    justify-content: space-around;
}
.row{
    flex-direction: row;
}
.column{
    flex-direction: column;
}
.j-center{
    justify-content: center;
}
.a-center{
    align-items: center;
}
.wrap{
    flex-wrap: wrap;
}
.no-wrap{
    flex-wrap: nowrap;
}
.absolute{
    position: absolute;
}
.relative{
    position: relative;
}
.back{
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 800px;
}
.title_category{
    color: white;
    font-weight: 300;
    font-size: 64px;
}
.gap{
    gap: 60px;
}