@import url('https://fonts.googleapis.com/css2?family=Literata:wght@300;400;500;600;700;800&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: 'Literata';
  font-style: normal;
  font-weight: 500;
}
body{
  min-width:520px;
  background-color:black;
}

li{
    padding:10px;
    list-style: none;
    color:white;
    transition: all 0.5s ease-out;
    font-size: 20px;
    font-weight: 400;
}
li:hover{
    color:#FFCB8E;
    transition: all 0.5s ease-out;
}
