.header{
    background: linear-gradient( rgba(255, 255, 255, 0.3), rgba(0, 0, 0, 0.9) ),url("/src/components/header/image/background.png");
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 800px;
}

.logo-search_form{
 margin-bottom: 30px ;
 background-color: black;
}
.logo{
font-size: 24px;
line-height: 150%;
letter-spacing: 0.15em;
color:#FFCB8E;
}
input{
    outline: none;
    background-color:black;
    border: none;
    color:white;
    margin-top: 11px;
}

input::placeholder{
    color:white;
    font-size: 20px;
}
.search{
    top:10px;
    right:180px;
}

nav{
    background-color:black;
    display: flex;
    justify-content: center;
}


.header_info{
    padding: 100px;
    gap:50px;
    color:white;
    
}
.header h1{
   text-align: center;
   text-transform: uppercase;
   font-size: 36px;
   font-weight: 700;
}
.header h1::first-letter{
    color:#FFCB8E;
}
p{
    text-align: center;
    font-size: 24px;
}
button{
margin-top: 50px;
box-sizing: border-box;
display: flex;
justify-content: center;
align-items: center;
width: 316px;
height: 80px;
border: 3px solid #FFCB8E;
background-color:transparent;
color:#FFCB8E;
font-size: 24px;
font-weight: 600;
transition: all 500ms ease; 

}
button:hover {
	background: #FFCB8E(0,0,0,0);
	color:white;
	box-shadow: inset 0 0 0 3px #FFCB8E;
}

.opener{
    display: none;
    width: 40px;
    height: 40px;
    background-color:black;
    border-radius:50%;
    position: relative;
    left:46%;
    bottom:20px;
}
.icon_active{
    stroke: white;
}


@media(max-width:600px){
    nav{
        background-color:transparent;
        
    }
    nav ul{
       display: flex;
       flex-direction: column;
       align-items: center;
       gap:20px;
       
    }
    nav ul li{
       border:1px solid black;
       border-radius: 45px;
       width: 300px;
       text-align: center;
    }
    .opener{
    display: block;
    }
}
